// src/pages/interview.js
import Head from "next/head";
import { useState } from "react";
import Image from "next/image";

export default function InterviewPage() {
    const [step, setStep] = useState(1); // Tracks current step (1, 2, or 3)
    const [formData, setFormData] = useState({
        fullName: "",
        email: "",
        phone: "",
        linkedin: "",
        answers: {},
        resume: null,
        coverLetter: null,
    });
    const [currentQuestion, setCurrentQuestion] = useState(0); // Tracks current question in step 2

    // Sample questions (move to src/data/questions.js later)
    const questions = [
        {
            id: 1,
            text: "Apart from English, which of the following languages do you speak?",
            options: ["French", "German", "Portuguese", "Mandarin", "None"],
        },
        // Add other 18 questions here as needed
        {
            id: 2,
            text: "What grade did you score in High school (or similar)?",
            options: ["A", "B", "C", "D"],
        },
        // Placeholder for brevity; expand with full list later
    ];

    const handleNext = () => {
        if (step === 1) setStep(2);
        else if (step === 2 && currentQuestion < questions.length - 1) {
            setCurrentQuestion(currentQuestion + 1);
        } else if (step === 2) setStep(3);
        else if (step === 3) {
            // Submit logic here (e.g., API call to /api/submit)
            console.log("Form submitted:", formData);
        }
    };

    const handleBack = () => {
        if (step === 2 && currentQuestion > 0) setCurrentQuestion(currentQuestion - 1);
        else if (step > 1) setStep(step - 1);
    };

    const handleChange = (e) => {
        const { name, value, files } = e.target;
        if (files) {
            setFormData({ ...formData, [name]: files[0] });
        } else if (step === 2) {
            setFormData({
                ...formData,
                answers: { ...formData.answers, [currentQuestion]: value },
            });
        } else {
            setFormData({ ...formData, [name]: value });
        }
    };

    return (
        <>
            <Head>
                <title>Growthpad Interview Process</title>
                <meta name="description" content="Complete your Growthpad interview" />
            </Head>
            <div className="min-h-screen bg-gray-100 flex flex-col justify-center items-center">
                <div className="max-w-2xl w-full mx-auto p-6 bg-white rounded-lg shadow-md">
                    {/* Logo */}
                    <div className="mb-6 text-center">
                        <Image
                            src="/assets/images/logo.svg"
                            alt="Growthpad Consulting Group Logo"
                            width={200}
                            height={200}
                            className="mx-auto"
                        />
                    </div>

                    {/* Progress Bar */}
                    <div className="mb-6">
                        <div className="w-full bg-gray-200 rounded-full h-2">
                            <div
                                className="bg-[#f05d23] h-2 rounded-full"
                                style={{ width: `${(step / 3) * 100}%` }}
                            ></div>
                        </div>
                        <p className="text-sm text-[#231812] mt-2">
                            Step {step} of 3
                        </p>
                    </div>

                    {/* Step 1: Initial Form */}
                    {step === 1 && (
                        <div>
                            <h2 className="text-2xl font-bold text-[#231812] mb-4">
                                Letâ€™s Get Started
                            </h2>
                            <div className="space-y-4">
                                <input
                                    type="text"
                                    name="fullName"
                                    value={formData.fullName}
                                    onChange={handleChange}
                                    placeholder="Full Name"
                                    className="w-full p-2 border border-gray-300 rounded focus:outline-none focus:border-[#f05d23]"
                                />
                                <input
                                    type="email"
                                    name="email"
                                    value={formData.email}
                                    onChange={handleChange}
                                    placeholder="Email"
                                    className="w-full p-2 border border-gray-300 rounded focus:outline-none focus:border-[#f05d23]"
                                />
                                <input
                                    type="tel"
                                    name="phone"
                                    value={formData.phone}
                                    onChange={handleChange}
                                    placeholder="Phone"
                                    className="w-full p-2 border border-gray-300 rounded focus:outline-none focus:border-[#f05d23]"
                                />
                                <input
                                    type="text"
                                    name="linkedin"
                                    value={formData.linkedin}
                                    onChange={handleChange}
                                    placeholder="LinkedIn Profile (e.g., https://linkedin.com/in/...)"
                                    className="w-full p-2 border border-gray-300 rounded focus:outline-none focus:border-[#f05d23]"
                                />
                            </div>
                        </div>
                    )}

                    {/* Step 2: Questionnaire */}
                    {step === 2 && (
                        <div>
                            <h2 className="text-2xl font-bold text-[#231812] mb-4">
                                Question {currentQuestion + 1}/{questions.length}
                            </h2>
                            <p className="text-lg text-[#231812] mb-4">
                                {questions[currentQuestion].text}
                            </p>
                            <div className="space-y-4">
                                {questions[currentQuestion].options.map((option) => (
                                    <label
                                        key={option}
                                        className="flex items-center p-2 hover:bg-gray-100 rounded"
                                    >
                                        <input
                                            type="radio"
                                            name={`q${currentQuestion}`}
                                            value={option}
                                            checked={formData.answers[currentQuestion] === option}
                                            onChange={handleChange}
                                            className="mr-2 accent-[#f05d23]"
                                        />
                                        {option}
                                    </label>
                                ))}
                            </div>
                        </div>
                    )}

                    {/* Step 3: End Form */}
                    {step === 3 && (
                        <div>
                            <h2 className="text-2xl font-bold text-[#231812] mb-4">
                                Submit Your Documents
                            </h2>
                            <div className="space-y-4">
                                <div>
                                    <label className="block text-[#231812] mb-1">
                                        Resume (PDF or DOCX)
                                    </label>
                                    <input
                                        type="file"
                                        name="resume"
                                        accept=".pdf,.docx"
                                        onChange={handleChange}
                                        className="w-full p-2 border border-gray-300 rounded"
                                    />
                                </div>
                                <div>
                                    <label className="block text-[#231812] mb-1">
                                        Cover Letter (PDF or DOCX)
                                    </label>
                                    <input
                                        type="file"
                                        name="coverLetter"
                                        accept=".pdf,.docx"
                                        onChange={handleChange}
                                        className="w-full p-2 border border-gray-300 rounded"
                                    />
                                </div>
                            </div>
                        </div>
                    )}

                    {/* Navigation */}
                    <div className="mt-6 flex justify-between">
                        <button
                            onClick={handleBack}
                            disabled={step === 1}
                            className="px-4 py-2 bg-gray-400 text-white rounded disabled:opacity-50 hover:bg-gray-500 transition"
                        >
                            Back
                        </button>
                        <button
                            onClick={handleNext}
                            className="px-4 py-2 bg-[#f05d23] text-white rounded hover:bg-[#d94f1e] transition"
                        >
                            {step === 3 ? "Submit" : "Next"}
                        </button>
                    </div>
                </div>
            </div>
        </>
    );
}